import React from 'react';
import s from './Header.module.scss';
import logo from './../../CommonFiles/Assets/logo.svg';
import { HashLink as Link } from 'react-router-hash-link';

let Header = () => {
    return (
        <div className={s.container}>
            <div className={s.wrapper}>
                <div className={s.logoBlock}>
                    <img className={s.imageLogo} src={logo} alt="" />
                </div>
                {window.innerWidth <= '360' ?
                <div></div> :
                 <div className={s.headerMenu}>
                    <p><Link to="/mainPage#aboutMe">About me</Link></p>
                    <p><Link to="/mainPage#relationships">Relationships</Link></p>
                    <p><Link to="/mainPage#requirements">Requirements</Link></p>
                    <p><Link to="/mainPage#users">Users</Link></p>
                    <p><Link to="/mainPage#signUp">Sign Up</Link></p>
                </div>}
            </div>
        </div>
    )
}

export default Header;