import React from 'react';
import s from './MainBanner.module.scss';
import banner from './../../CommonFiles/Assets/banner-photo.jpg';
import { HashLink as Link } from 'react-router-hash-link';

let MainBanner = () => {
    return (
        <div className={s.container} id="requirements">
            <div className={s.wrapper}>
               <div className={s.imgWrapper}>
                    <img src={banner} className={s.imgBanner} alt=""/>
                </div>
                <div className={s.infoWrapper}>
                    <p className={s.headerText}>TEST ASSIGNMENT FOR FRONTEND DEVELOPER POSITION</p>
                    <p className={s.mainText}>{window.innerWidth <= '360' ?
                     'We kindly remind you that your test assignment should be submitted as a link to github/bitbucket repository.'
                     : 'We kindly remind you that your test assignment should be submitted as a link to github/bitbucket repository. Please be patient, we consider and respond to every application that meets minimum requirements. We look forward to your submission. Good luck! The photo has to scale in the banner area on the different screens'}</p>
                    <button className={s.btnSingUp}>
                        <Link to="/mainPage#formAnchor">Sing up now</Link>
                    </button>
                </div>
            </div>
        </div>
    )
}

export default MainBanner;